// material
import { Container, Typography, Box, Button, Stack, useTheme, styled, Grid, DialogContent, useMediaQuery, LinearProgress } from '@mui/material';
import { motion } from 'framer-motion';
import { useState, useEffect, useRef } from 'react';
import useSound from 'use-sound';
import Confetti from 'react-dom-confetti';
import Countdown from 'react-countdown';
import Lottie from 'react-lottie';

// hooks
import useSettings from '../hooks/useSettings';

// components
import Page from '../components/Page';
import { varWrapEnter, varFadeInUp, varFadeInRight, DialogAnimate, varZoomInUp, ButtonAnimate, varFadeInLeft, varZoomIn} from '../components/animate';
import ScratchCard from '../components/react-scratchcard/dist';
import NFTCard from '../components/main/NFTCard';
import LastGameTable from '../components/main/game/LastGameTable';

import SuccessCheckLottie from '../assets/lottie/successCheck.json';
import FailedLottie from '../assets/lottie/failed.json';
import LoadingLottie from '../assets/lottie/loading.json';

// SFX
import winSound from '../assets/sfx/winGame.mp3';
import loseSound from '../assets/sfx/loseGame.mp3';
import popDingSound from '../assets/sfx/popDing.wav';
import UseBonusScratch from '../components/UseBonusScratch';
import ApproveButton from '../components/ApproveButton';
import { fCRCurrency } from '../utils/formatNumber';
// ----------------------------------------------------------------------

import { useEthers,  useCalls, useContractFunction, useTokenAllowance, useTokenBalance} from '@usedapp/core';

import { utils } from 'ethers';
import { SHAZU_NFT_ADDRESS, SHAZU_NFT_CONTRACT, PANCAKE_ROUTER_V2_ADDRESS, ROUTER_CONTRACT, WBNB_ADDRESS, BUSD_ADDRESS, SHAZU_ADDRESS, SHAZU_CONTRACT} from '../config';


const AnimateContainer = styled(motion.div)(({ theme }) => ({

}));

const successCheckOptions = {
  loop: false,
  autoplay: true,
  animationData: SuccessCheckLottie,
  background: 'transparent'
};

const failedOptions = {
  loop: false,
  autoplay: true,
  animationData: FailedLottie,
  background: 'transparent'
};

const loadingLottieOptions = {
  loop: true,
  autoplay: true,
  animationData: LoadingLottie,
  background: 'transparent'
};

const NFT_COLLECTION = {
  common: {
      name: 'Orange Ninja',
      rarity: 'Common',
      video: '/static/SHAZU/nft/ORANGE_NINJA.mp4',
      smallThumbnail: '/static/SHAZU/nft/thumbnail/nft_thumbnail_orange.jpg'
  },
  rare: {
      name: 'Blue Ninja',
      rarity: 'Rare',
      video: '/static/SHAZU/nft/BLUE_NINJA.mp4',
      smallThumbnail: '/static/SHAZU/nft/thumbnail/nft_thumbnail_blue.jpg'
  },
  superrare: {
      name: 'Green Ninja',
      rarity: 'Super Rare',
      video: '/static/SHAZU/nft/GREEN_NINJA.mp4',
      smallThumbnail: '/static/SHAZU/nft/thumbnail/nft_thumbnail_green.jpg'
  },
  ultimaterare: {
      name: 'Purple Ninja',
      rarity: 'Ultimate Rare',
      video: '/static/SHAZU/nft/PURPLE_NINJA.mp4',
      smallThumbnail: '/static/SHAZU/nft/thumbnail/nft_thumbnail_purple.jpg'
  },
}

const NFT_DESCRIPTION = {
  1: "Though the most common, one of the most needed in your journey. This Orange Belt Ninja will allow you to have standard capabilities to punch your way through to staking.",
  2: "With the ability to punch, comes with the ability to kick. A common kick but a rare breed of form. The color blue signifies the peace with your inner self. You are nearly there, you are not a novice anymore.",
  3: "Super Rare Green Belt with Super Rare Kick Combos. Congratulations, you are now ranking up and have the ability to do combos. Every ninja must be ready to stake and earn $SHAZU.",
  4: "You have mastered the art of Ninja Spirit! Gravity does not matter anymore. Welcome to the Dojo. It is now time to earn and reap the rewards."
}
const confettiConfig = {
  angle: 90,
  spread: 360,
  startVelocity: 40,
  elementCount: "120",
  dragFriction: 0.12,
  duration: 5000,
  stagger: 3,
  width: "10px",
  height: "10px",
  perspective: "700px",
  colors: ["#a864fd", "#29cdff", "#78ff44", "#ff718d", "#fdff6a"]
};

// Random component
const Completionist = () => <Typography variant='body2'>Refresh first to play!</Typography>;

export default function GamePage() {


  const { account } = useEthers()

  const getAccount = account ? utils.getAddress(account) : undefined;

  const sleep = (milliseconds) => {
    console.info('...');
    return new Promise(resolve => setTimeout(resolve, milliseconds))
  };

  const handleReload = () => {
    window.location.reload();
  }

  const [canvasFlag, setCanvasFlag] = useState(true);
  const handleRestart = () => {
    setCanvasFlag(canvasFlag+1);
    
    setIsScratching(false)

    setScratching(false)


    setScratchIdSetuped(false)

    setShowAfterPlay(false)
    // set the another initialstate here
  }

  const { themeStretch } = useSettings();
  const theme = useTheme();

  const bigdesktop = useMediaQuery(theme.breakpoints.up("xl"));
  const desktop = useMediaQuery(theme.breakpoints.up("lg"));
  const tablet = useMediaQuery(theme.breakpoints.up("md"));
  const mobile = useMediaQuery(theme.breakpoints.up("sm"));
  const smallMobile = useMediaQuery(theme.breakpoints.up("xs"));

  const gradientButtonStyle = {
    height: '2.5rem',
    fontSize: '1rem',
    fontWeight: '600',
    width: '100%'
  }

  const canvasSizes = () => {
    if(bigdesktop) return '450px';
    if(desktop) return '450px';
    if(tablet) return '450px';
    if(mobile) return '450px';
    if(smallMobile) return '330px';
  };

  const [requests] = useCalls([
    {
    contract: SHAZU_NFT_CONTRACT,
    address: SHAZU_NFT_ADDRESS ,
    method: 'getRequestLength',
    args: [getAccount]
    }
  ]);


  const [scratchId, setScratchId] = useState(undefined)
  const [scratchIdSetuped, setScratchIdSetuped] = useState(false);
  const [scratching, setScratching] = useState(false)

  useEffect(() => {
    if (requests && requests.value && !scratchIdSetuped) {
      setScratchId(requests.value.toString())
      setScratchIdSetuped(true);
    }
  }, [requests])


  
  const [balance, allowance, scratch, price] = useCalls([
    {
      contract: SHAZU_CONTRACT,
      address: SHAZU_ADDRESS,
      method: 'balanceOf',
      args: [getAccount]
    },
    {
      contract: SHAZU_CONTRACT,
      address: SHAZU_ADDRESS,
      method: 'allowance',
      args: [getAccount, SHAZU_NFT_ADDRESS]
    },
    {
    contract: SHAZU_NFT_CONTRACT,
    address: SHAZU_NFT_ADDRESS ,
    method: 'scratches',
    args: [getAccount, scratchId]
    },
    {
      contract: ROUTER_CONTRACT,
      address: PANCAKE_ROUTER_V2_ADDRESS,
      method: 'getAmountsIn',
      args: [utils.parseEther('7'), [SHAZU_ADDRESS, WBNB_ADDRESS, BUSD_ADDRESS]]
    },
  ]);

  console.log('balance')
  console.log(balance)

  useEffect(async () => {
    if (scratch && scratch.value && scratching && scratch.value.blockNumber.gt(0)) {


      setIsScratching(true);
      console.log('...')
      console.log(scratch.value)

    
      if (scratch.value.rarity == 0) {
        setWinStatus(false)
      }

      let rarity = 0

      if (scratch.value.rarity == 1) {
        rarity = NFT_COLLECTION.common
      }


      if (scratch.value.rarity == 2) {
        rarity = NFT_COLLECTION.rare
      }

      if (scratch.value.rarity == 3) {
        rarity = NFT_COLLECTION.superrare
      }

      if (scratch.value.rarity == 4) {
        rarity = NFT_COLLECTION.ultimaterare
      }


      if (rarity !== 0) {
        setWinningNFT({
          nft: rarity,
          nftId: scratch.value.tokenId.toString(),
          id: scratch.value.tokenId.toString(),
          description: NFT_DESCRIPTION[scratch.value.rarity],
        })
      }

      setLoadingStatus({progress: 100, message: 'Done'});
    }
  }, [scratch, scratching])
  // SFX
  const [playWinSound, { stopWinSound }] = useSound(winSound, {volume: 0.7, interrupt: true});
  const [playLoseSound, { stoploseSound }] = useSound(loseSound, {volume: 0.7, interrupt: true});
  
  const canvasSettings = {
    image: '/static/SHAZU/background/ScratchCard.jpg',
    finishPercent: 35,
    onComplete: async () => handleFinishScratch()
  };

  const [openPlayResultDialog, setOpenPlayResultDialog] = useState(false);
  const [showAfterPlay, setShowAfterPlay] = useState(false);

  // APPROVE SHAZU
  const [isApproved, setIsApproved] = useState(false);
  
  // LOADING DIALOG
  const [openLoadingDialog, setOpenLoadingDialog] = useState(false);
  const [loadingStatus, setLoadingStatus] = useState({ progress: 0, message: 'Message 1' });

  // SCRATCHING
  const [isScratching, setIsScratching] = useState(false);

  const { state, send } = useContractFunction(SHAZU_NFT_CONTRACT, 'scratch', {
    transactionName: 'Scratch'
  });

  const handleStartScratch = async () => {
    // blockchain confirmation
    setOpenLoadingDialog(true)
    send({gasLimit: 500000});
    setIsScratching(true);

    setLoadingStatus({progress: 10, message: 'Scratching Initialized (Pending Signature)'});
  }

  useEffect(() => {
    if (state.status === 'Exception') {
        setIsScratching(false);
        setOpenLoadingDialog(false)
    }

    if (state.status === 'Fail') {
        setIsScratching(false);
        setOpenLoadingDialog(false)
    }

    if (state.status === 'None') {
        setIsScratching(false);
        setOpenLoadingDialog(false)
    }

    if (state.status === 'Success') {
        setOpenLoadingDialog(false)
        setScratching(true)
    }

    if (state.status == 'Mining') {
        setLoadingStatus({progress: 50, message: 'Mining'});
      }
  }, [state, getAccount]);


  const handleFinishScratch = async () => {

    if (winStatus) {
      playWinSound();
    } else {
      playLoseSound();
    }

    setOpenPlayResultDialog(true);
    setShowAfterPlay(true);

    setBonusScratchTicketAmount({ ...bonusScratchTicketAmount, remaining: bonusScratchTicketAmount.remaining-1 });
  }

  const [ownedSNC, setOwnedSNC] = useState(true);
  const [scratchTicketAmount, setScratchTicketAmount] = useState(1);
  const [bonusScratchTicketAmount, setBonusScratchTicketAmount] = useState({ remaining: 1, total: 9 });

  const handleClaim = () => {
    setOpenPlayResultDialog(false);
  }

    // true if win, false if lose
  const [winStatus, setWinStatus] = useState(true);



  const [winningNFT, setWinningNFT] = useState(false)


   // STATUS DIALOG
  const [playPopDingSound, { stopPopDingSound }] = useSound(popDingSound);
  const [openStatusDialog, setOpenStatusDialog] = useState({ open: false, success: false });
  const [statusDialogMsg, setStatusDialogMsg] = useState('');
  const [statusDialogInfo, setStatusDialogInfo] = useState('');
  const [failedStatusMsg, setFailedStatusMsg] = useState('');

  const handleOpenSuccessDialog = (msg) => {
      playPopDingSound();
      setStatusDialogMsg(msg);
      setOpenStatusDialog({ open: true, success: true });
  }

  const handleOpenFailedDialog = (msg, reason) => {
      playPopDingSound();
      setStatusDialogMsg(msg);
      setFailedStatusMsg(reason);
      setOpenStatusDialog({ open: true, success: false });
  }

  const handleCloseStatusDialog = () => {
      setOpenStatusDialog({ ...openStatusDialog, open: false });
      setStatusDialogMsg('');
      setFailedStatusMsg('');
  }

  const handleBuy = (e) => {
      setScratchTicketAmount(scratchTicketAmount+1);
      handleOpenSuccessDialog('Buy Scratch Successful')
      // handleOpenFailedDialog('Buy NFT Failed', 'failed reason here');
  }
  
  return (
    <Page title="SHAZU - Game" style={{ overflow: 'hidden'}}>
      {winStatus 
        ?
        <div style={{ height: '0px', width: '0px', position: 'relative', marginLeft: 'auto', marginRight: 'auto', zIndex: '100' }}>
          <Confetti active={ openPlayResultDialog } config={ confettiConfig } />
        </div>
        :
        ''
      }

      <AnimateContainer initial="initial" animate="animate" variants={varWrapEnter}>
        <motion.div variants={varFadeInUp}>
          <Stack alignItems="center" style={{ marginTop: '2rem' }}>
            <img src="/static/SHAZU/3d/sword.png" alt="" style={{ width: '12rem', marginBottom: '-5rem'}} />
            <div className="gradient-card top-gradient-bar" style={{ marginTop: '5rem', width: 'fit-content', padding: '1rem', marginInline: 'auto', maxWidth: '50rem', textAlign: 'center' }}>
              <Typography className="neo-font" style={{ lineHeight: '1', marginBottom: '0.4rem', fontSize: '3rem' }}>
                Scratch <span className="yellow-color">Card</span>
              </Typography>
              <Typography style={{ opacity: '0.6' }}>
                Welcome to the Scratch 2 Mint Game. Our first game awaits for you to play. Collect all 4 action cards (BSC) to enter the DOJO.
              </Typography>
            </div>
          </Stack>
        </motion.div>

        <motion.div variants={varFadeInLeft} className="p-relative w-100">
          <img src="/static/SHAZU/abstract/cloud-left.svg" alt="" style={{ position: 'absolute', width: '10rem', top: '2rem' }} />
        </motion.div>

        <motion.div variants={varFadeInUp}> 
          <section style={{ background: 'url(/static/SHAZU/abstract/line-bg.png)', backgroundSize: '100% 100%', paddingTop: '5rem', paddingBottom: '5rem' }}>
            <Container maxWidth={themeStretch ? false : 'md'}>
              <Grid container spacing={2} alignItems="stretch" direction="row" justifyContent="flex-start">
                <Grid item xs={12} sx={{ mt: 2 }}>
                  <Stack spacing={2} style={{ width: 'min-content', marginLeft:'auto', marginRight: 'auto'}}>
                      {/* START SCRATCHING */}
                      {isScratching && winStatus &&
                        <motion.div key={canvasFlag} variants={varFadeInUp} className="scratchcard-container" style={{ width: canvasSizes(), height: '540px',  overflow: 'hidden', position: 'relative', borderRadius: '12px', WebkitTransform: 'translateZ(0)'}}>
                          <ScratchCard {...canvasSettings} width={450} height={540}>
                            <img src='/static/SHAZU/background/winScratch.jpg' alt="" style={{ width: '100%', height: '99%', margin: 'auto' }} />
                          </ScratchCard>
                        </motion.div>
                      }

                      {isScratching && !winStatus &&
                        <motion.div variants={varFadeInUp} className="scratchcard-container" style={{ width: canvasSizes(), height: '540px',  overflow: 'hidden', position: 'relative', borderRadius: '12px', WebkitTransform: 'translateZ(0)'}}>
                          <ScratchCard {...canvasSettings} width={450} height={540}>
                            <img src='/static/SHAZU/background/loseScratch.jpg' alt="" style={{ width: '100%', height: '99%', margin: 'auto' }} />
                          </ScratchCard>
                        </motion.div>
                      }

                      {/* PRE SCRATCH */}
                      {!isScratching &&
                        <div style={{ width: canvasSizes(), height: '540px',  overflow: 'hidden', position: 'relative', borderRadius: '12px', WebkitTransform: 'translateZ(0)'}}>
                          <div style={{ width: '100%', height: '100%', position: 'relative' }}>
                            <img src="/static/SHAZU/background/ScratchCard.jpg" alt="" style={{ width: '100%', height: '99%', margin: 'auto', zIndex: '3', objectFit: 'cover' }} />
                            <div style={{ backgroundColor: '#00000080', display: 'flex', justifyContent: 'center', alignItems: 'center', zIndex: '4', position: 'absolute', width: '100%', height: '100%', left: '0', top: '0', backdropFilter: 'blur(2px)' }}>
                              <Stack style={{ width: '70%' }}>

                                { account ? (
                                isApproved || (price && price.value && allowance && allowance.value[0].gt(price.value[0][0]))
                                  ?(
                                  <Button onClick={handleStartScratch} size="large" variant="contained" color="yellow" style={{ width: '100%' }} disabled={balance && balance.value[0] && price && price.value && balance.value[0].lt(price.value[0][0])}>
                                    Scratch
                                  </Button>
                                  ):(
                                  <ApproveButton setIsApproved={setIsApproved}/>                                  
                                )
                                ):(
                                  ''
                                )}
                                
                                
                                {price && price.value && (
                                <div style={{ textAlign: 'right', marginTop: '0.4rem' }}>
                                  Fee:&nbsp;
                                  <span style={{ fontWeight: '700'}}>
                                    {fCRCurrency(utils.formatEther((price.value[0][0])))} SHAZU <img src="/static/SHAZU/ShazuCoin.png" alt="" style={{ width: '1rem', display: 'inline-block', verticalAlign: 'text-top' }} />
                                  </span>
                                </div>
                                )}
                                { account ? (
                                isApproved || (price && price.value && allowance && allowance.value[0].gt(price.value[0][0])) ? (
                                  balance && balance.value[0] && price && price.value && balance.value[0].lt(price.value[0][0]) ? (
                                    <div style={{ textAlign: 'center', marginTop: '2rem', opacity: '0.7', fontWeight: '600', visibility: isApproved ? 'hidden' : 'visible' }}>
                                      You dont have enough SHAZU
                                    </div>
                                  ):(
                                    ''
                                  )
                                ):(
                                <div style={{ textAlign: 'center', marginTop: '2rem', opacity: '0.7', fontWeight: '600', visibility: isApproved ? 'hidden' : 'visible' }}>
                                  You need to approve<br/>SHAZU first to scratch!
                                </div>
                                )
                                ):(
                                  <div style={{ textAlign: 'center', marginTop: '2rem', opacity: '0.7', fontWeight: '600', visibility: isApproved ? 'hidden' : 'visible' }}>
                                  You need to connect wallet<br/>first to scratch!
                                </div>
                                )}
                              </Stack>
                            </div>
                          </div>
                        </div>
                      }

                        <motion.Box variants={varFadeInUp} 
                          className="gradient-card top-gradient-bar" 
                          style={{ display: 'flex', flexDirection: 'column', width: '100%', maxWidth: '45rem', padding: '1rem', marginLeft: 'auto', minWidth: '20rem' }}
                        >
                          
                          {
                            (showAfterPlay) ?
                            (
                            <Button variant="contained" color="yellow" onClick={handleRestart} style={{ marginInline: 'auto' }}>
                              Play again
                            </Button>
                            ) : (
                              <>
                                <UseBonusScratch setOpenLoadingDialog = {setOpenLoadingDialog}  setLoadingStatus = {setLoadingStatus} setScratching = {setScratching}/>
                              </>
                            )
                          }
                          
                        </motion.Box>
                  </Stack>
                </Grid>
              </Grid>
            </Container>
          </section>
        </motion.div>

        <motion.div variants={varFadeInRight} className="p-relative w-100">
          <img src="/static/SHAZU/abstract/cloud-right.svg" alt="" style={{ position: 'absolute', width: '10rem', top: '-7rem', right: '0'}} />
        </motion.div>

        {
          <Container maxWidth={themeStretch ? false : 'xl'}>
          <motion.div variants={varFadeInUp} className="top-side-faded-bar" style={{ padding: '1.4rem 0', marginTop: '5rem', marginBottom: '10rem', paddingTop: '4rem'}}>
            <div className="gradient-card left-gradient-bar" style={{ width: 'fit-content', padding: '1rem', marginBottom: '1rem' , zIndex: '3', position: 'relative' }}>
              <Typography className="neo-font" style={{ lineHeight: '1', marginBottom: '0.4rem', fontSize: '3rem' }}>
                Last <span className="yellow-color">Games</span>
              </Typography>
            </div>

            <div className="p-relative w-100 floating">
                <img src="/static/SHAZU/abstract/gradient-blob3.png" alt="" className="game-blob1" />
            </div>

            <div className="p-relative w-100 text-center" style={{ display: 'flex', justifyContent: 'center' }}>
              <img src="/static/SHAZU/abstract/line-crown1.svg" alt="" className="game-crown1" />
            </div> 

            <div className="p-relative w-100 text-center" style={{ display: 'flex', justifyContent: 'center' }}>
              <img src="/static/SHAZU/abstract/line-crown1.svg" alt="" className="game-crown2" />
            </div> 

            <LastGameTable/>

          </motion.div>
        </Container>
      }

      </AnimateContainer>

      {/* LOADING DIALOG */}
      <DialogAnimate open={openLoadingDialog} maxWidth="xs" animate={varZoomInUp} scroll="paper">
        <DialogContent style={{ textAlign: 'center' }}>
            <Lottie 
                options={loadingLottieOptions}
                height={150}
                width={150}
                isClickToPauseDisabled
            />
            <Typography variant='h4'> 
                Scratch Loading
            </Typography>

            <Stack style={{ marginTop: '1rem' }}>
                <LinearProgress variant="determinate" value={loadingStatus.progress} color="warning" />
                <div style={{ marginTop: '1rem', marginBottom: '1rem'}}>
                    {loadingStatus.message}
                </div>
            </Stack>
        </DialogContent>
      </DialogAnimate>

      {/* FINISH SCRATCH DIALOG */}
      <DialogAnimate open={openPlayResultDialog} maxWidth="xs" animate={varZoomInUp} scroll="paper" onClose={() => !winStatus ? setOpenPlayResultDialog(false) : ''}>
        <DialogContent style={{ textAlign: 'center', position: 'relative', zIndex: '2' }}>
          {winStatus
            ?
            <>
              <Typography variant='h3'>
                Congratulations! <span role='img' aria-label='mindblown emoji'></span>
              </Typography>
              <Typography variant='h5' style={{ marginTop: '1rem', marginBottom: '0.4rem', fontWeight: '500' }}>
                You won:
              </Typography>

              {/* <NFTCard unknown name={collection.name} rarity={collection.rarity} id={collection.id} video={collection.video} /> */}
              <div style={{ zIndex: '3' }}>
                <NFTCard collection={winningNFT} />
              </div>
              <ButtonAnimate style={{ width: '100%', marginTop: '3rem' }}>
                <Button variant="contained" style={gradientButtonStyle} color="purple" onClick={handleClaim}>
                    Claim
                </Button>
              </ButtonAnimate>
            </>
            :
            <>
              <span role='img' aria-label='sad' style={{ fontSize: '5rem' }}></span>
              <Typography variant='h4'>Better luck next time...</Typography>
              <Button variant="container" style={{ marginTop: '3rem' }} onClick={() => setOpenPlayResultDialog(false)}>Close</Button>
            </>
          }                
        </DialogContent>
      </DialogAnimate>

      {/* STATUS DIALOG */}
      <DialogAnimate open={openStatusDialog.open} maxWidth="xs" animate={varZoomInUp} scroll="paper" onClose={handleCloseStatusDialog}>
        <DialogContent style={{ textAlign: 'center' }}>
            {
                openStatusDialog.success 
                ?
                <div className="successCheck-lottie-container">
                    <Lottie options={successCheckOptions} isClickToPauseDisabled />
                </div>
                :
                <div className="successCheck-lottie-container">
                    <Lottie options={failedOptions} isClickToPauseDisabled />
                </div>
            }
            
            <Typography variant="h3">
                {statusDialogMsg}
            </Typography>
            
            {
                openStatusDialog.success 
                ?
                ''
                :
                <div>
                    {failedStatusMsg}
                </div>
            }
        </DialogContent>
    </DialogAnimate>
    </Page>
  );
}
