import { TableRow,TableCell, Stack } from '@mui/material';

import { shortenAddress, shortenTransactionHash  } from '@usedapp/core';
import { fCRCurrency } from '../../../utils/formatNumber';

const LASTGAMECOLUMNS = [
    { id: 'block', label: 'Block', minWidth: 100, align: 'center', width: '20%'},
    { id: 'user', label: 'User', minWidth: 100, align: 'center', width: '20%'},
    { id: 'transaction', label: 'Transaction', minWidth: 100, align: 'center', width: '20%'},
    { id: 'results', label: 'Results', minWidth: 100, align: 'center', width: '30%'},
    { id: 'nft', label: 'NFT', minWidth: 100, align: 'center', width: '30%'}
]

const formatHash = (hash) => {
    const completeHash = hash;
    const startHash = completeHash.slice(0, 3);
    const endHash = completeHash.slice(completeHash.length-5 - 8);
    const formattedHash = `${startHash}...${endHash}`;
    return formattedHash;
}

export default function LastGameTableItem({ items, index }){
    return(
        <>
            {items.map((row, index) => (
                <TableRow hover role="checkbox" tabIndex={-1} key={`lastgamerow${index}`}>
                    {LASTGAMECOLUMNS.map((column, cellIndex) => {
                        const value = row[column.id];
                        
                        if(column.id === 'block'){
                            return (
                                <TableCell align={column.align} key={`lastgamecell${cellIndex}`}>
                                    <a href={`https://bscscan.com/block/${value}`} target="_blank" rel="noreferrer" style={{ color: 'white', fontWeight: '700', fontSize: '1rem' }} >
                                        {value}
                                    </a>
                                </TableCell>
                            );
                        }

                        if(column.id === 'transaction'){
                            return (
                                <TableCell align={column.align} key={`lastgamecell${cellIndex}`}>
                                    <a href={`https://bscscan.com/tx/${value}`} target="_blank" rel="noreferrer" style={{ color: 'white', fontWeight: '700', fontSize: '1rem' }} >
                                        {formatHash(value)}
                                    </a>
                                </TableCell>
                            );
                        }


                        if(column.id === 'user'){
                            return (
                                <TableCell align={column.align} key={`lastgamecell${cellIndex}`}>
                                    <a href={`https://bscscan.com/address/${value}`} target="_blank" rel="noreferrer" style={{ color: 'white', fontWeight: '700', fontSize: '1rem' }} >
                                        {shortenAddress(value)}
                                    </a>
                                </TableCell>
                            );
                        }

                        if(column.id === 'results'){
                            if(value === true){
                                return (
                                    <TableCell align={column.align} key={`lastgamecell${cellIndex}`} style={{ fontWeight: '700', fontSize: '1rem'}}>
                                        Win <span role='img' aria-label='trophy'></span>
                                    </TableCell>
                                );
                            }

                            if(value === false){
                                return (
                                    <TableCell align={column.align} key={`lastgamecell${cellIndex}`} style={{ fontWeight: '700', fontSize: '1rem'}}>
                                        Lose <span role='img' aria-label='lose'></span>
                                    </TableCell>
                                );
                            }     
                        }

                        if(column.id === 'nft'){
                            
                            if(row.results === true){
                                let nftBorder = '';
                                if(value.rarity === 'Common'){
                                    nftBorder = '2px solid var(--shazu-yellow)';
                                }else if(value.rarity === 'Rare'){
                                    nftBorder = '2px solid var(--shazu-blue)';
                                }else if(value.rarity === 'Super Rare'){
                                    nftBorder = '2px solid var(--shazu-green)';
                                }else if(value.rarity === 'Ultimate Rare'){
                                    nftBorder = '2px solid var(--shazu-purple)';
                                }
                                return (
                                    <TableCell align={column.align} key={`lastgamecell${cellIndex}`} style={{ fontSize: '1rem' }}>
                                        <Stack direction="row" spacing={2} style={{ justifyContent: column.align }}>
                                            <img src={value.smallThumbnail} alt='' style={{ width: '2rem', height: '2rem', borderRadius: '0.3rem', marginRight: '0.4rem', border: nftBorder }} />
                                            <a href={`https://opensea.io/assets/${row.nftId}`} target="_blank" rel="noreferrer" style={{ color: 'white', fontWeight: '700', fontSize: '1rem' }} >
                                                {row.nftId}
                                            </a>
                                        </Stack>
                                    </TableCell>
                                );
                            }

                            // IF LOSE
                            if(row.results === false){
                                return (
                                    <TableCell align={column.align} key={`lastgamecell${cellIndex}`} style={{ fontSize: '1rem' }}>
                                        <span style={{ backgroundColor: 'rgba(255, 255, 255, 0.08)', padding: '0rem 0.75rem', borderRadius: '4px' }}>---</span>
                                    </TableCell>
                                );
                            }
                        }

                        return(
                            ''
                        )
                        
                    })}
                </TableRow>
            ))}
        </>
    )
}