import { useState, useEffect, useRef } from 'react';
import { Container, Typography, Box, Button, Stack, useTheme, styled, Grid, DialogContent, useMediaQuery, LinearProgress } from '@mui/material';

import { useEthers,  useCalls, useContractFunction} from '@usedapp/core';
import { SHAZU_NFT_ADDRESS, SHAZU_NFT_CONTRACT} from '../config';

import useClaims from '../hooks/useClaims';



export default function UseBonusScratch(props) {

    const { account } = useEthers();

    const [isScratching, setIsScratching] = useState(false);

    const data = useClaims('gameh');

    const startTimeStamp = 1653868800;
    const dayLength = 86400

    const currentSeconds = new Date().getTime() / 1000;

    const coeff = Math.floor((((currentSeconds - startTimeStamp) / dayLength) + 1));


    const [freeMint, bonus] = useCalls([
        {
        contract: SHAZU_NFT_CONTRACT,
        address: SHAZU_NFT_ADDRESS ,
        method: 'freeMint',
        args: []
        },
        {
         contract: SHAZU_NFT_CONTRACT,
        address: SHAZU_NFT_ADDRESS ,
        method: 'bonusScratches',
        args: [account, coeff]
        },
    ]);

    console.log('bonus')
    console.log(bonus)


    useEffect(() => {
        if (data && data.amount !== '') {
    
          setBonusNFT(parseInt(data.amount.toString()))
        }
      }, [data, account]);

    const [bonusNFT, setBonusNFT] = useState(0)

    const handleStartScratch = async () => {
        // blockchain confirmation
        props.setOpenLoadingDialog(true)
        send(data.index, account, data.amount, data.proof, {gasLimit: 700000});
        setIsScratching(true);

        props.setLoadingStatus({progress: 10, message: 'Scratching Initialized (Pending Signature)'});
    }

    const { state, send } = useContractFunction(SHAZU_NFT_CONTRACT, 'scratchWithBonus', {
        transactionName: 'Bonus Scratch'
    });

    useEffect(() => {
        if (state.status === 'Exception') {
            setIsScratching(false);
            props.setOpenLoadingDialog(false)
        }
    
        if (state.status === 'Fail') {
            setIsScratching(false);
            props.setOpenLoadingDialog(false)
        }
    
        if (state.status === 'None') {
            setIsScratching(false);
            props.setOpenLoadingDialog(false)
        }
    
        if (state.status === 'Success') {
            props.setLoadingStatus({progress: 100, message: 'Done'});
            props.setOpenLoadingDialog(false)
            props.setScratching(true)
        }

        if (state.status == 'Mining') {
            props.setLoadingStatus({progress: 50, message: 'Mining'});
          }
      }, [state, account]);

    return (
        <>
        { 

        (bonus && bonus.value && freeMint && freeMint.value && bonus && parseInt(bonus.value.toString()) < bonusNFT * parseInt(freeMint.value.toString())) ? (
        <Stack alignItems="center" style={{ marginTop: '1rem' }}>
        <div>
          you have
        </div>

        { freeMint && freeMint.value && bonus && bonus.value && (
        <div style={{fontSize: '1.4rem', textAlign: 'center', fontWeight: '700', marginTop: '-0.4rem' }}>
          {bonusNFT * parseInt(freeMint.value.toString()) - parseInt(bonus.value.toString())} / {bonusNFT * parseInt(freeMint.value.toString())} <span className="bold-yellow">BONUS</span> scratches left
        </div>
        )}

        <div style={{ fontSize: '0.8rem', opacity: '0.6', textAlign: 'center' }}>
          You got this bonus from owning the SNC NFT
        </div>

        <Button onClick={handleStartScratch} disabled={isScratching} variant="contained" size="large" color="yellow" style={{ marginTop: '1rem' }}>
          Use Bonus Scratch
        </Button>
      </Stack>
        ) : (
            ''
            )
     }
     </>
    )
}