// material
import { Table, TableRow, TableHead, TableBody, TableCell, TableContainer, Pagination, Stack } from '@mui/material';
import { shortenAddress, shortenTransactionHash  } from '@usedapp/core';
import { useState, useEffect } from 'react';
import usePaginator from 'react-use-paginator';

// components
import Scrollbar from '../../Scrollbar';
import LastGameTableItem from './LastGameTableItem';

import { LAST_MINT_NFT} from '../../../queries';
import { useQuery } from '@apollo/client';

// ----------------------------------------------------------------------

const LASTGAMECOLUMNS = [
    { id: 'block', label: 'Block', minWidth: 100, align: 'center', width: '20%'},
    { id: 'user', label: 'User', minWidth: 100, align: 'center', width: '20%'},
    { id: 'transaction', label: 'Transaction', minWidth: 100, align: 'center', width: '20%'},
    { id: 'results', label: 'Results', minWidth: 100, align: 'center', width: '30%'},
    { id: 'nft', label: 'NFT', minWidth: 100, align: 'center', width: '30%'}
]


const formatHash = (hash) => {
    const completeHash = hash;
    const startHash = completeHash.slice(0, 3);
    const endHash = completeHash.slice(completeHash.length-5 - 8);
    const formattedHash = `${startHash}...${endHash}`;
    return formattedHash;
}

const NFT_COLLECTION = {
    common: {
        name: 'Orange Ninja',
        rarity: 'Common',
        video: '/static/SHAZU/nft/ORANGE_NINJA.mp4',
        smallThumbnail: '/static/SHAZU/nft/thumbnail/nft_thumbnail_orange.jpg'
    },
    rare: {
        name: 'Blue Ninja',
        rarity: 'Rare',
        video: '/static/SHAZU/nft/BLUE_NINJA.mp4',
        smallThumbnail: '/static/SHAZU/nft/thumbnail/nft_thumbnail_blue.jpg'
    },
    superrare: {
        name: 'Green Ninja',
        rarity: 'Super Rare',
        video: '/static/SHAZU/nft/GREEN_NINJA.mp4',
        smallThumbnail: '/static/SHAZU/nft/thumbnail/nft_thumbnail_green.jpg'
    },
    ultimaterare: {
        name: 'Purple Ninja',
        rarity: 'Ultimate Rare',
        video: '/static/SHAZU/nft/PURPLE_NINJA.mp4',
        smallThumbnail: '/static/SHAZU/nft/thumbnail/nft_thumbnail_purple.jpg'
    },
}


export default function LastGameTable() {

    const { data } = useQuery(LAST_MINT_NFT, {
        pollInterval: 3000
    });

    const [nft, setNft] = useState(undefined);
    
    useEffect(() => {
        if (data && data.scratches) {
            const listedData = data.scratches.map((row) => ({
                block: row.block,
                transaction: row.transactionHash,
                user: row.owner.id,
                nft: row.rarity == 1 ? NFT_COLLECTION.common : row.rarity == 2 ? NFT_COLLECTION.rare : row.rarity == 3 ? NFT_COLLECTION.superrare : NFT_COLLECTION.ultimaterare,
                nftId: '#'+parseInt(row.token.id),
                results: row.rarity != 0
            }))

            setNft(listedData)
        }
    }, [data])
    // PAGINATION
    const { Component: LastGameTableItemComponent, nextPage, prevPage, setPageIndex, totalPages } = usePaginator({
        PageComponent: LastGameTableItem,
        maxPerPage: 10,
        data: nft
    });

    const [paginationPage, setPaginationPage] = useState(1);
    const handlePaginationChange = (event, value) => {
        setPaginationPage(value);
        setPageIndex(value);
    }    
    return (
        <>
            <Scrollbar>
                <TableContainer sx={{ minWidth: '400px', maxWidth: '90rem' , backgroundColor: '#1C1C1C', border: 'solid 1px #3B3B3B', borderRadius: '8px', padding: '1rem', marginTop: '1rem' }} style={{ marginLeft: 'auto', marginRight: 'auto' }}>
                    <Table>
                        <TableHead sx={{  backgroundColor: '#1C1C1C', borderRadius: '12px' }}>
                            <TableRow> 
                                {LASTGAMECOLUMNS.map((column) => (
                                    <TableCell key={column.id} align={column.align} style={{ minWidth: column.minWidth, width: column.width, boxShadow: 'none'}} sx={{ background: '#272727', fontSize: '1rem', color: 'white' }}>
                                        {column.label}
                                    </TableCell>
                                ))}
                            </TableRow>
                        </TableHead>
                        <TableBody>
                            <LastGameTableItemComponent />
                        </TableBody>
                    </Table>
                </TableContainer>
            </Scrollbar>

            <Stack style={{  width: '100%', marginTop: '1rem' }} alignItems="center" >
                <Pagination 
                    color="yellow" 
                    count={totalPages} page={paginationPage}
                    onChange={handlePaginationChange}
                />
            </Stack>
        </>
    );              
}
