import { useState, useEffect } from 'react';
import { utils, BigNumber, constants} from 'ethers';

import { Container, Typography, Box, Button, Stack, useTheme, styled, Grid, DialogContent, useMediaQuery, LinearProgress } from '@mui/material';

import { useContractFunction, useEthers } from '@usedapp/core';

import { SHAZU_CONTRACT, SHAZU_NFT_ADDRESS } from '../config';

const ApproveButton = (props) => {
  const [disabled, setDisabled] = useState(true);

  const [buttonText, setButtonText] = useState('Approve Shazu');

  const value = constants.MaxUint256


const { state, send } = useContractFunction(SHAZU_CONTRACT, 'approve', { transactionName: 'Approve SHAZU' });

  useEffect(() => {
    if (state.status === 'Exception' || state.status === 'Fail') {
    
      setDisabled(false)
      setButtonText('Approve SHAZU');
    }

    if (state.status === 'None') {
        setDisabled(false)
      setButtonText('Approve SHAZU');
    }

    if (state.status === 'Success') {
      setDisabled(false)
      setButtonText('Approve SHAZU');

      props.setIsApproved(true)
    }

    if (state.status === 'Mining') {
      setDisabled(true);
      setButtonText('Approving...');
    }
  }, [state]);

  const handleApprove = () => {
    send(SHAZU_NFT_ADDRESS, value);
  };

  return (
    <Button onClick={handleApprove} size="large" variant="contained" color="yellow" style={{ width: '100%' }} disabled={disabled}>
        {buttonText}
    </Button>
  );
};

export default ApproveButton;
